/* roboto-regular - cyrillic */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-v20-cyrillic-regular.eot"); /* IE9 Compat Modes */
  src: local(""),
    url("../fonts/roboto-v20-cyrillic-regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("../fonts/roboto-v20-cyrillic-regular.woff2")
      format("woff2"),
    /* Super Modern Browsers */ url("../fonts/roboto-v20-cyrillic-regular.woff")
      format("woff"),
    /* Modern Browsers */ url("../fonts/roboto-v20-cyrillic-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../fonts/roboto-v20-cyrillic-regular.svg#Roboto") format("svg"); /* Legacy iOS */
}
